<template>
   <div>
      <div v-show="!loading">
         <h2 class="pd-10-top title">{{ internationalDict.lastPromotion }}</h2>
         <!-- pc -->
         <div class="full-height container" v-if='windowSize === "big"'>

            <h2 class="sub-title">{{ internationalDict.recentoffers }}</h2>
            <el-carousel :interval="4000" type="card" class="carousel-container">
               <el-carousel-item v-for="(item, index) of promotions" :key="'promotions' + index">
                  <img :src="item.img" :alt="item.alt" class="carousel-item" @load="imageOnload">
               </el-carousel-item>
            </el-carousel>
         </div>
         <!-- mobile -->
         <div class="container" v-if='windowSize === "small" || windowSize === "mobile"'>
            <h2 class="sub-title">{{ internationalDict.recentoffers }}</h2>
            <div class="product-table">
               <div v-for="(item, index) of promotions" :key="'promotions' + index">
                  <div class='product-table-item'>
                     <div class="flex1 flex-row-center">
                        <img :src="item.img" :alt="item.alt" width="100%" height="100%" @load="imageOnload">
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </div>
      <loading v-show="loading" class="loading"></loading>
   </div>
</template>
<script>
import loading from '@/components/loading.vue'
import { promotionImg } from '../assets/googleFormData/apiData'
export default {
   name: 'hot-sale',
   components: {
      loading
   },
   mixins: [],
   props: {

   },
   data() {
      return {
         loading: false,
         imageReadys: 1,
         promotions: []
      }
   },
   computed: {
      internationalDict() {
         return this.$store.getters.getInternationalDict
      },
      windowSize() {
         return this.$store.getters.getWindowSize;
      }
   },
   watch: {

   },
   mounted() {
      this.getPromotionsList()
   },
   methods: {
      getPromotionsList() {
         const sheetsId = "1nAPsK9BKDFN5O_LdGka_EtKWNMEla9ey0lBQeCqdTX0"
         const sheet = 'sheet1!A1:D10'
         const apiKey = process.env.VUE_APP_GOOGLEKEY
         const baseUrl = "https://drive.google.com/uc?export=view&id="
         fetch(`https://sheets.googleapis.com/v4/spreadsheets/${sheetsId}/values/${sheet}?key=${apiKey}`)
            .then(response => {
               return response.json()
            })
            .then(res => {
               if (!res.values.length) return;
               const header = res.values[0]
               const content = res.values.map(item => {
                  let value = {}
                  header.forEach((cell, index) => {
                     value[cell] = item[index]
                  })
                  value.img = baseUrl + value.id
                  return value
               })
               this.promotions = content.slice(1)
            }).catch(err => {
               this.promotions = promotionImg
               console.error(err)
            })
      },
      imageOnload() {
         this.imageReadys++
         if (this.imageReadys >= this.promotions.length) {
            this.loading = false
         }
      }
   }
};
</script>
<style scoped>
@media screen and (min-width: 1150px) {
   .product-table-item {
      --product-width: 40vw;
   }
}

@media screen and (max-width: 1150px) {
   .product-table-item {
      --product-width: 90vw;
   }
}

.product-table {
   padding-top: 1em;
   overflow: hidden;
   display: flex;
   flex-wrap: wrap;
   gap: 2em;
   justify-content: center;
   align-items: center;
}

.product-table-item {
   display: flex;
   width: var(--product-width);
   aspect-ratio: 16 / 9;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}

.product-text {
   padding-top: 5px;
   color: var(--primary-color);
   font-weight: 600;
}

.carousel-container {
   height: 100%;
   aspect-ratio: 1080 / 1080;
}

.carousel-item {
   height: 100%;
   object-fit: contain;
}

.hot-sale-split {
   padding: 0 10px;
   font-size: 20px;
   font-weight: 600;
}

.hot-sale-tags {
   font-size: 20px;
   font-weight: 600;
   cursor: pointer;
}

.hot-sale-tags-active {
   color: var(--primary-color);
}

:deep(.el-carousel__mask) {
   opacity: 0.5;
}
</style>
